.page-body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 21px;
  color: #000000;
}
.wrapper {
  width: auto;
  margin: 0 16px;
}
@media (min-width: 768px) {
  .wrapper {
    margin: 0 34px;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    width: 1180px;
    margin: 0 auto;
  }
}
.price-room {
  background: linear-gradient(68.56deg, rgba(170, 203, 240, 0.25) 11.33%, rgba(170, 203, 240, 0.75) 64.1%), url("../img/bg-img.png");
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 24px;
  padding-bottom: 49px;
  min-height: 100vh;
}
@media (min-width: 768px) {
  .price-room {
    padding-top: 27px;
    padding-bottom: 39px;
  }
}
@media (min-width: 1280px) {
  .price-room {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.price-room-title {
  font-family: Roboto, sans-serif;
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .price-room-title {
    font-size: 36px;
    line-height: 55px;
    font-weight: 500;
  }
}
.price-room-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 32px;
  column-gap: 22px;
  list-style: none;
  padding: 0;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .price-room-container {
    row-gap: 37px;
    column-gap: 28px;
  }
}
@media (min-width: 1280px) {
  .price-room-container {
    row-gap: 43px;
    column-gap: 32px;
  }
}
.price-room__room {
  position: relative;
  max-width: 288px;
  min-height: 347px;
  border-radius: 13px;
  overflow: hidden;
}
@media (min-width: 768px) {
  .price-room__room {
    max-width: 336px;
    min-height: 420px;
  }
}
@media (min-width: 1280px) {
  .price-room__room {
    max-width: 372px;
    min-height: 372px;
  }
}
.price-room__image-wrapper {
  width: 100%;
  height: 100%;
}
.price-room__image-wrapper::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(10, 34, 64, 0.1) 0%, #0A2240 100%);
  z-index: 1;
}
.price-room__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.price-room__info {
  display: flex;
  flex-direction: column;
  gap: 40px;
  position: absolute;
  left: 0;
  bottom: 0;
  margin: 21px;
  z-index: 2;
}
@media (min-width: 768px) {
  .price-room__info {
    gap: 37px;
    margin: 24px;
  }
}
.price-room__info-block {
  display: flex;
  flex-direction: column;
}
.price-room__info-block--desc {
  position: relative;
  gap: 10px;
}
.price-room__info-block--desc::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  bottom: -20px;
  left: 0;
  background-color: rgba(255, 255, 255, 0.2);
}
.price-room__info-block--price {
  gap: 16px;
}
@media (min-width: 1280px) {
  .price-room__info-block--price {
    flex-direction: row;
  }
}
.price-room__name {
  font-family: Roboto, sans-serif;
  font-size: 20px;
  line-height: 26px;
  font-weight: 700;
  color: #FFFFFF;
  text-decoration: none;
  margin: 0;
  padding: 0;
  cursor: pointer;
  max-height: 82px;
  overflow: hidden;
}
@media (min-width: 768px) {
  .price-room__name {
    font-size: 24px;
    line-height: 32px;
    font-weight: 700;
    max-height: 102px;
  }
}
.price-room__name:hover {
  text-decoration: underline;
}
.price-room__name:active,
.price-room__name:focus {
  text-decoration: none;
  opacity: 0.5;
}
.price-room__text {
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #FFFFFF;
  margin: 0;
  padding: 0;
  max-height: 68px;
  overflow: hidden;
}
@media (min-width: 768px) {
  .price-room__text {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    max-height: 72px;
  }
}
.price-room__price {
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  color: #FFFFFF;
  margin: 0;
  padding: 0;
}
.price-room__price--amount {
  font-size: 20px;
  line-height: 32px;
  font-weight: 700;
  color: #FF4B77;
}
.price-room__price--currency {
  font-size: 20px;
  line-height: 32px;
  font-weight: 400;
  color: #999999;
}
@media (min-width: 768px) {
  .price-room__price {
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
  }
  .price-room__price--amount {
    font-size: 24px;
  }
  .price-room__price--currency {
    font-size: 23px;
  }
}
.price-room__room--discount:hover::before {
  position: absolute;
  content: "";
  top: 24px;
  left: 24px;
  height: 30px;
  width: 213px;
  background-image: url("../img/icon-discount.svg");
  background-repeat: no-repeat;
}
.price-room__book-info--none {
  display: none;
}
.price-room__room--book {
  background-color: rgba(255, 255, 255, 0.4);
}
.price-room__room--book:hover::before {
  display: none;
}
.price-room__room--book::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: none;
  background-color: rgba(255, 255, 255, 0.4);
  z-index: 3;
}
.price-room__room--book .price-room__book-info--none {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 153px;
  width: 100%;
  background-color: #3C3E43;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-align: center;
  color: #FFFFFF;
  z-index: 4;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .price-room__room--book .price-room__book-info--none {
    height: 162px;
  }
}
@media (min-width: 1280px) {
  .price-room__room--book .price-room__book-info--none {
    height: 98px;
  }
}
.price-room__room--book .price-room__book-link {
  color: #FF4B77;
  text-decoration: none;
  cursor: pointer;
}
.price-room__room--book .price-room__book-link:hover {
  text-decoration: underline;
}
.price-room__room--book .price-room__book-link:active,
.price-room__room--book .price-room__book-link:focus {
  text-decoration: none;
  color: #FF97B0;
}
.price-room__book-button {
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  text-align: center;
  color: #FFFFFF;
  background-color: #F91155;
  border: none;
  border-radius: 3px;
  width: 100%;
  padding: 8px 24px 10px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .price-room__book-button {
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
    border-radius: 5px;
    padding: 11px 24px 13px;
  }
}
@media (min-width: 1280px) {
  .price-room__book-button {
    width: auto;
  }
}
.price-room__book-button:hover {
  background-color: #D8003F;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.price-room__book-button:active,
.price-room__book-button:focus {
  color: #F9637C;
  background-color: #D8003F;
  box-shadow: none;
}
