.price-room {
    background: linear-gradient(68.56deg, fade(@light-blue-bd-page, 25%) 11.33%, fade(@light-blue-bd-page, 75%) 64.1%), 
                url("../img/bg-img.png");
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    padding-top: 24px;
    padding-bottom: 49px;
    min-height: 100vh;
    @media (min-width:@tablet-size) {
        padding-top: 27px;
        padding-bottom: 39px;
    }
    @media (min-width:@desktop-size) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.price-room-title {
    font-family: Roboto, sans-serif;
    .font-style(24px; 32px; 700);
    color: @black;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    padding: 0;
    margin-bottom: 24px;
    @media (min-width:@tablet-size) {
        .font-style(36px; 55px; 500);
    }
}

.price-room-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 32px;
    column-gap: 22px;
    list-style: none;
    padding: 0;
    margin: 0 auto;
    @media (min-width:@tablet-size) {
        row-gap: 37px;
        column-gap: 28px;
    }
    @media (min-width:@desktop-size) {
        row-gap: 43px;
        column-gap: 32px;
    }
}

.price-room__room {
    position: relative;
    max-width: 288px;
    min-height: 347px;
    border-radius: 13px;
    overflow: hidden;
    @media (min-width:@tablet-size) {
        max-width: 336px;
        min-height: 420px;
    }
    @media (min-width:@desktop-size) {
        max-width: 372px;
        min-height: 372px;
    }
}

.price-room__image-wrapper {
    width: 100%;
    height: 100%;
    &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, fade(@blue-image-wrapper, 10%) 0%, @blue-image-wrapper 100%);
        z-index: 1;
    }
}

.price-room__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.price-room__info {
    display: flex;
    flex-direction: column;
    gap: 40px;
    position: absolute;
    left: 0;
    bottom: 0;
    margin: 21px;
    z-index: 2;
    @media (min-width:@tablet-size) {
        gap: 37px;
        margin: 24px;
    }
}

.price-room__info-block {
    display: flex;
    flex-direction: column;
    &--desc {
        position: relative;
        gap: 10px;
    }
    &--desc::after {
        position: absolute;
        content: "";
        width: 100%;
        height: 1px;
        bottom: -20px;
        left: 0;
        background-color: fade(@white, 20%);
    }
    &--price {
        gap: 16px;
        @media (min-width:@desktop-size) {
            flex-direction: row;
        }
    }
}

.price-room__name {
    font-family: Roboto, sans-serif;
    .font-style(20px; 26px; 700);
    color: @white;
    text-decoration: none;
    margin: 0;
    padding: 0;
    cursor: pointer;
    max-height: 82px;
    overflow: hidden;
    @media (min-width:@tablet-size) {
        .font-style(24px; 32px; 700);
        max-height: 102px;
    }
    &:hover {
        text-decoration: underline;
    }
    &:active,
    &:focus {
        text-decoration: none;
        opacity: 0.5;
    }
}

.price-room__text {
    font-family: Open Sans, sans-serif;
    .font-style(14px; 21px; 400);
    color: @white;
    margin: 0;
    padding: 0;
    max-height: 68px;
    overflow: hidden;
    @media (min-width:@tablet-size) {
        .font-style(16px; 24px; 400);
        max-height: 72px;
    }
}

.price-room__price {
    font-family: Open Sans, sans-serif;
    .font-style(14px; 21px; 700);
    color: @white;
    margin: 0;
    padding: 0;
    &--amount {
        .font-style(20px; 32px; 700);
        color: @pink-standart;
    }
    &--currency {
        .font-style(20px; 32px; 400);
        color: @grey-currency;
    }
    @media (min-width:@tablet-size) {
        .font-style(16px; 24px; 700);
        &--amount {
            font-size: 24px;
        }
        &--currency {
            font-size: 23px;
        }
    }
}
